'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Checkbox } from '@/components/ui/checkbox'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'

const collegeRecommendations = [
  {
    id: 1,
    name: 'Harvard University',
    location: 'Cambridge, MA',
    type: 'Private',
    size: 'Medium',
    acceptanceRate: 3.4,
    avgGPA: 4.18,
    avgSAT: 1520,
    avgACT: 34,
    tuition: 54000,
    programs: ['Pre-Med', 'Neuroscience', 'Public Health', 'Psychology'],
    strengths: ['Research Opportunities', 'Medical School Placement', 'Alumni Network'],
    matchScore: 95,
    financialAid: 'Need-blind, meets 100% need',
    campusLife: 'Urban, Historic',
    diversity: 'High',
    researchOpportunities: 'Extensive'
  },
  {
    id: 2,
    name: 'Johns Hopkins University',
    location: 'Baltimore, MD',
    type: 'Private',
    size: 'Medium',
    acceptanceRate: 7.5,
    avgGPA: 3.93,
    avgSAT: 1520,
    avgACT: 34,
    tuition: 58000,
    programs: ['Pre-Med', 'Biomedical Engineering', 'Public Health', 'Neuroscience'],
    strengths: ['Medical Research', 'Hospital Affiliation', 'STEM Programs'],
    matchScore: 92,
    financialAid: 'Need-blind, generous aid',
    campusLife: 'Urban, Research-focused',
    diversity: 'High',
    researchOpportunities: 'World-class'
  },
  {
    id: 3,
    name: 'University of California, Berkeley',
    location: 'Berkeley, CA',
    type: 'Public',
    size: 'Large',
    acceptanceRate: 14.5,
    avgGPA: 3.89,
    avgSAT: 1430,
    avgACT: 32,
    tuition: 14000,
    programs: ['Pre-Med', 'Public Health', 'Psychology', 'Biomedical Engineering'],
    strengths: ['Research Excellence', 'Diverse Programs', 'Value'],
    matchScore: 88,
    financialAid: 'In-state friendly, merit scholarships',
    campusLife: 'Suburban, Diverse',
    diversity: 'Very High',
    researchOpportunities: 'Abundant'
  },
  {
    id: 4,
    name: 'Duke University',
    location: 'Durham, NC',
    type: 'Private',
    size: 'Medium',
    acceptanceRate: 6.2,
    avgGPA: 4.13,
    avgSAT: 1510,
    avgACT: 34,
    tuition: 60000,
    programs: ['Pre-Med', 'Neuroscience', 'Public Policy', 'Psychology'],
    strengths: ['Medical Center', 'Research', 'School Spirit'],
    matchScore: 85,
    financialAid: 'Need-blind, no-loan policy',
    campusLife: 'Suburban, Traditional',
    diversity: 'High',
    researchOpportunities: 'Strong'
  },
  {
    id: 5,
    name: 'University of Michigan',
    location: 'Ann Arbor, MI',
    type: 'Public',
    size: 'Large',
    acceptanceRate: 20.2,
    avgGPA: 3.88,
    avgSAT: 1420,
    avgACT: 32,
    tuition: 16000,
    programs: ['Pre-Med', 'Public Health', 'Psychology', 'Biomedical Engineering'],
    strengths: ['Strong Alumni Network', 'Research', 'School Spirit'],
    matchScore: 82,
    financialAid: 'Good in-state value, merit aid',
    campusLife: 'College town, Vibrant',
    diversity: 'High',
    researchOpportunities: 'Extensive'
  }
]

export default function CollegeMatchPage() {
  const [formData, setFormData] = useState({
    gpa: '',
    satScore: '',
    actScore: '',
    intendedMajor: '',
    preferredSize: '',
    preferredType: '',
    location: '',
    maxTuition: '',
    priorities: [] as string[],
    extracurriculars: [] as string[]
  })
  const [showResults, setShowResults] = useState(false)
  const [currentStep, setCurrentStep] = useState(1)
  const totalSteps = 4

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }))
  }

  const handleCheckboxChange = (field: 'priorities' | 'extracurriculars', value: string, checked: boolean) => {
    setFormData(prev => ({
      ...prev,
      [field]: checked 
        ? [...prev[field], value]
        : prev[field].filter(item => item !== value)
    }))
  }

  const handleSubmit = () => {
    setShowResults(true)
  }

  const getMatchColor = (score: number) => {
    if (score >= 90) return 'text-green-600'
    if (score >= 80) return 'text-blue-600'
    if (score >= 70) return 'text-yellow-600'
    return 'text-red-600'
  }

  const getMatchLabel = (score: number) => {
    if (score >= 90) return 'Excellent Match'
    if (score >= 80) return 'Good Match'
    if (score >= 70) return 'Fair Match'
    return 'Reach School'
  }

  if (showResults) {
    return (
      <div className="min-h-screen bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          {/* Header */}
          <div className="text-center mb-12">
            <h1 className="text-4xl font-bold text-gray-900 mb-4">
              Your College Matches
            </h1>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Based on your preferences and profile, here are colleges that align with your healthcare career goals.
            </p>
          </div>

          {/* Results */}
          <div className="space-y-6">
            {collegeRecommendations.map(college => (
              <Card key={college.id} className="hover:shadow-lg transition-shadow">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-2xl font-bold text-gray-900">
                        {college.name}
                      </CardTitle>
                      <CardDescription className="text-lg">
                        {college.location} • {college.type} • {college.size}
                      </CardDescription>
                    </div>
                    <div className="text-right">
                      <div className={`text-2xl font-bold ${getMatchColor(college.matchScore)}`}>
                        {college.matchScore}%
                      </div>
                      <div className="text-sm text-gray-600">
                        {getMatchLabel(college.matchScore)}
                      </div>
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {/* Academic Stats */}
                    <div>
                      <h4 className="font-semibold text-gray-900 mb-3">Academic Profile</h4>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span>Acceptance Rate:</span>
                          <span>{college.acceptanceRate}%</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Avg GPA:</span>
                          <span>{college.avgGPA}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Avg SAT:</span>
                          <span>{college.avgSAT}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Avg ACT:</span>
                          <span>{college.avgACT}</span>
                        </div>
                      </div>
                    </div>

                    {/* Programs & Strengths */}
                    <div>
                      <h4 className="font-semibold text-gray-900 mb-3">Healthcare Programs</h4>
                      <div className="flex flex-wrap gap-1 mb-3">
                        {college.programs.map(program => (
                          <Badge key={program} variant="secondary" className="text-xs">
                            {program}
                          </Badge>
                        ))}
                      </div>
                      <div className="space-y-1">
                        {college.strengths.map(strength => (
                          <div key={strength} className="text-sm text-gray-600">
                            • {strength}
                          </div>
                        ))}
                      </div>
                    </div>

                    {/* Additional Info */}
                    <div>
                      <h4 className="font-semibold text-gray-900 mb-3">Key Details</h4>
                      <div className="space-y-2 text-sm">
                        <div>
                          <span className="font-medium">Tuition:</span>
                          <br />${college.tuition.toLocaleString()}/year
                        </div>
                        <div>
                          <span className="font-medium">Financial Aid:</span>
                          <br />{college.financialAid}
                        </div>
                        <div>
                          <span className="font-medium">Research:</span>
                          <br />{college.researchOpportunities}
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className="flex gap-3 mt-6 pt-6 border-t">
                    <Button className="flex-1">
                      Learn More
                    </Button>
                    <Button variant="outline" className="flex-1">
                      Add to List
                    </Button>
                    <Button variant="outline" className="flex-1">
                      Compare
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>

          {/* Actions */}
          <div className="mt-12 text-center">
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button size="lg" onClick={() => setShowResults(false)}>
                Refine Search
              </Button>
              <Button variant="outline" size="lg">
                Save Results
              </Button>
              <Button variant="outline" size="lg">
                Get More Matches
              </Button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-gray-900 mb-4">
            College Match Tool
          </h1>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Find colleges that align with your healthcare career goals, academic profile, and personal preferences.
          </p>
        </div>

        {/* Progress Bar */}
        <div className="mb-8">
          <div className="flex justify-between text-sm text-gray-600 mb-2">
            <span>Step {currentStep} of {totalSteps}</span>
            <span>{Math.round((currentStep / totalSteps) * 100)}% Complete</span>
          </div>
          <Progress value={(currentStep / totalSteps) * 100} className="w-full" />
        </div>

        <Card>
          <CardHeader>
            <CardTitle>
              {currentStep === 1 && 'Academic Profile'}
              {currentStep === 2 && 'Career Interests'}
              {currentStep === 3 && 'College Preferences'}
              {currentStep === 4 && 'Priorities & Activities'}
            </CardTitle>
            <CardDescription>
              {currentStep === 1 && 'Tell us about your academic achievements'}
              {currentStep === 2 && 'What healthcare field interests you most?'}
              {currentStep === 3 && 'What type of college environment do you prefer?'}
              {currentStep === 4 && 'What matters most to you in a college?'}
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {currentStep === 1 && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <Label htmlFor="gpa">GPA (4.0 scale)</Label>
                  <Input
                    id="gpa"
                    placeholder="e.g., 3.8"
                    value={formData.gpa}
                    onChange={(e) => handleInputChange('gpa', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="sat">SAT Score (optional)</Label>
                  <Input
                    id="sat"
                    placeholder="e.g., 1450"
                    value={formData.satScore}
                    onChange={(e) => handleInputChange('satScore', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="act">ACT Score (optional)</Label>
                  <Input
                    id="act"
                    placeholder="e.g., 32"
                    value={formData.actScore}
                    onChange={(e) => handleInputChange('actScore', e.target.value)}
                  />
                </div>
              </div>
            )}

            {currentStep === 2 && (
              <div className="space-y-4">
                <div>
                  <Label htmlFor="major">Intended Major/Field</Label>
                  <Select value={formData.intendedMajor} onValueChange={(value) => handleInputChange('intendedMajor', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select your intended field" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="pre-med">Pre-Medicine</SelectItem>
                      <SelectItem value="nursing">Nursing</SelectItem>
                      <SelectItem value="public-health">Public Health</SelectItem>
                      <SelectItem value="psychology">Psychology</SelectItem>
                      <SelectItem value="biomedical-engineering">Biomedical Engineering</SelectItem>
                      <SelectItem value="neuroscience">Neuroscience</SelectItem>
                      <SelectItem value="pharmacy">Pharmacy</SelectItem>
                      <SelectItem value="physical-therapy">Physical Therapy</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>
            )}

            {currentStep === 3 && (
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <Label htmlFor="size">Preferred School Size</Label>
                  <Select value={formData.preferredSize} onValueChange={(value) => handleInputChange('preferredSize', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select size preference" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="small">Small (under 5,000)</SelectItem>
                      <SelectItem value="medium">Medium (5,000 - 15,000)</SelectItem>
                      <SelectItem value="large">Large (over 15,000)</SelectItem>
                      <SelectItem value="no-preference">No Preference</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="type">School Type</Label>
                  <Select value={formData.preferredType} onValueChange={(value) => handleInputChange('preferredType', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Select type preference" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="public">Public</SelectItem>
                      <SelectItem value="private">Private</SelectItem>
                      <SelectItem value="no-preference">No Preference</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="location">Location Preference</Label>
                  <Input
                    id="location"
                    placeholder="e.g., Northeast, California, No preference"
                    value={formData.location}
                    onChange={(e) => handleInputChange('location', e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="tuition">Max Annual Tuition</Label>
                  <Input
                    id="tuition"
                    placeholder="e.g., 50000"
                    value={formData.maxTuition}
                    onChange={(e) => handleInputChange('maxTuition', e.target.value)}
                  />
                </div>
              </div>
            )}

            {currentStep === 4 && (
              <div className="space-y-6">
                <div>
                  <Label className="text-base font-medium">What's most important to you? (Select all that apply)</Label>
                  <div className="grid grid-cols-2 gap-3 mt-3">
                    {[
                      'Research Opportunities',
                      'Medical School Placement',
                      'Financial Aid',
                      'Campus Life',
                      'Location',
                      'Diversity',
                      'Alumni Network',
                      'Hospital Affiliation'
                    ].map(priority => (
                      <div key={priority} className="flex items-center space-x-2">
                        <Checkbox
                          id={priority}
                          checked={formData.priorities.includes(priority)}
                          onCheckedChange={(checked) => handleCheckboxChange('priorities', priority, checked as boolean)}
                        />
                        <Label htmlFor={priority} className="text-sm">{priority}</Label>
                      </div>
                    ))}
                  </div>
                </div>

                <div>
                  <Label className="text-base font-medium">Current Extracurricular Activities</Label>
                  <div className="grid grid-cols-2 gap-3 mt-3">
                    {[
                      'Hospital Volunteering',
                      'Research Experience',
                      'Leadership Roles',
                      'Community Service',
                      'Sports',
                      'Arts/Music',
                      'Academic Clubs',
                      'Work Experience'
                    ].map(activity => (
                      <div key={activity} className="flex items-center space-x-2">
                        <Checkbox
                          id={activity}
                          checked={formData.extracurriculars.includes(activity)}
                          onCheckedChange={(checked) => handleCheckboxChange('extracurriculars', activity, checked as boolean)}
                        />
                        <Label htmlFor={activity} className="text-sm">{activity}</Label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}

            {/* Navigation Buttons */}
            <div className="flex justify-between pt-6 border-t">
              <Button
                variant="outline"
                onClick={() => setCurrentStep(Math.max(1, currentStep - 1))}
                disabled={currentStep === 1}
              >
                Previous
              </Button>
              
              {currentStep < totalSteps ? (
                <Button onClick={() => setCurrentStep(currentStep + 1)}>
                  Next
                </Button>
              ) : (
                <Button onClick={handleSubmit}>
                  Find My Matches
                </Button>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Info Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-12">
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Personalized Results</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-gray-600">
                Get college recommendations tailored to your academic profile and healthcare career goals.
              </p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Comprehensive Data</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-gray-600">
                Access detailed information about programs, admissions stats, and campus life.
              </p>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Expert Insights</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-gray-600">
                Benefit from insights gathered from successful healthcare students and professionals.
              </p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
